export function getPositionsForSport(sport: string): string[] {
  switch (sport) {
    case 'Football':
      return ['QB', 'RB', 'WR', 'TE', 'OL', 'DL', 'LB', 'CB', 'S', 'K', 'P', 'ATH'];
    case 'Baseball':
    case 'Softball':
      return ['P', 'C', '1B', '2B', '3B', 'SS', 'LF', 'CF', 'RF', 'UTIL'];
    case 'MensSoccer':
    case 'WomensSoccer':
      return ['GK', 'DEF', 'MID', 'FWD', 'Wing'];
    case 'MensBasketball':
    case 'WomensBasketball':
      return ['PG', 'SG', 'SF', 'PF', 'C', 'G', 'F'];
    case 'MensVolleyball':
    case 'WomensVolleyball':
      return ['S', 'OH', 'MB', 'OPP', 'L/DS'];
    case 'MensTrack':
    case 'WomensTrack':
      return ['Sprints', 'Middle Distance', 'Distance', 'Hurdles', 'Jumps', 'Throws', 'Multis'];
    case 'MensSwimming':
    case 'WomensSwimming':
      return ['Freestyle', 'Backstroke', 'Breaststroke', 'Butterfly', 'IM'];
    case 'MensTennis':
    case 'WomensTennis':
      return ['Singles', 'Doubles'];
    case 'MensLacrosse':
    case 'WomensLacrosse':
      return ['Attack', 'Midfield', 'Defense', 'Goalie'];
    case 'MensHockey':
    case 'WomensHockey':
      return ['Goalie', 'Defense', 'Forward'];
    case 'MensGolf':
    case 'WomensGolf':
      return ['Golfer'];
    case 'WomensCrossCountry':
    case 'MensCrossCountry':
      return ['Runner'];
    case 'MensGymnastics':
    case 'WomensGymnastics':
      return ['All-Around', 'Vault', 'Bars', 'Beam', 'Floor', 'Rings', 'Pommel', 'Parallel Bars', 'High Bar'];
    case 'Cheerleading':
      return ['Flyer', 'Base', 'Back Spot', 'Tumbler'];
    case 'Dance':
      return ['Jazz', 'Hip-Hop', 'Pom', 'Contemporary', 'Ballet', 'Tap'];
    case 'WomensFlagFootball':
      return ['QB', 'WR', 'RB', 'C', 'Rusher', 'DB', 'LB'];
    default:
      return [];
  }
}


